// import { socket } from "../SocketIO";
import CODE from "./opcode";
import socket from "./Socket";
import eventManager from "./EventManager";
// eslint-disable-next-line
import {
    ILoginResponse,
    IRegisterResponse,
    IServerInfoResponse,
    IHomeInfoResponse,
    IActiveVillageResponse,
    IMapResponse,
    IViewResponse,
    IProfileResponse,
    ISendMessageResponse,
    IViewPlusNoteResponse,
    InboxMessagesResponse,
    SentMessagesResponse,
    IViewMessageResponse,
    IProfileEditResponse
} from "./Response";

class Client {

    ready = false;
    authorized = false;
    response = {};
    villages = {};
    activeVillageName = "";
    activeVillageId = window.localStorage.getItem("vid") || 0;
    uid = window.localStorage.getItem("uid") || "";
    key = window.localStorage.getItem("key") || "";
    serverNumber = -1;

    constructor() {
        eventManager.create("ready");
    }

    // -- POST
    /** @returns {Promise<ILoginResponse>} */
    login(data = {}) {
        return socket.request(CODE.LOGIN, data);
        // return socket.request("login", data);
    }
    /** @returns {Promise<IRegisterResponse>} */
    register(data = {}) {
        return socket.request(CODE.REGISTER, data);
        // return socket.request("register", data);
    }
    /**@param {function({servers: IServerInfoResponse[]}): void} onLoad */
    listServers(onLoad) {
        this.GET(CODE.LIST_SERVERS, onLoad);
        // socket.cached("listServers", {}, onLoad, onLoad);
    }
    /**@param {function(IHomeInfoResponse): void} onLoad */
    homePage(onLoad) {
        return this.GET(CODE.HOME_PAGE, onLoad);
        // return socket.cached("homePage", {}, onLoad, onLoad);
    }
    /**@param {function(IActiveVillageResponse): void} onLoad */
    village(onLoad) {
        this.GET(CODE.PLAYER_VILLAGE, village => {
            this.activeVillageId = village.id;
            this.activeVillageName = village.name;
            onLoad(village);
        });
        // socket.cached("playerVillage", {}, onLoad, onLoad);
    }
    /**@returns {Promise<IMapResponse>} */
    mapViewNormal(vId = this.activeVillageId) {
        return socket.request(CODE.MAP_VIEW_NORMAL, { id: Math.floor((vId >= 1 && vId <= 10201) ? vId : 1) });
        // this.GET(CODE.MAP_VIEW_NORMAL, onLoad, { id: Math.floor((vId >= 1 && vId <= 10201) ? vId : 1) });
        // socket.cached("mapViewNormal", { id: vId }, onLoad, onLoad, vId);
    }
    /**@returns {Promise<IViewResponse>} */
    viewLand(vId = this.activeVillageId) {
        return socket.request(CODE.VIEW_LAND, { id: vId });
    }
    /**@returns {Promise<IProfileResponse>} */
    viewProfile(pId = 0) {
        return socket.request(CODE.VIEW_PROFILE, { id: pId });
    }
    /**@returns {Promise<IProfileEditResponse>} */
    editProfile() {
        return socket.request(CODE.EDIT_PROFILE, {});
    }
    /**@returns {Promise<Boolean>} */
    saveProfile(data = {}) {
        return socket.request(CODE.SAVE_PROFILE, data);
    }

    /**@returns {Promise<InboxMessagesResponse>} */
    viewInbox(page = 0) {
        return socket.request(CODE.VIEW_INBOX, { page });
    }
    /**@returns {Promise<SentMessagesResponse>} */
    viewSentMessages(page = 0) {
        return socket.request(CODE.VIEW_SENT_MESSAGES, { page });
    }
    /**@returns {Promise<ISendMessageResponse>} */
    sendMessage(data) {
        return socket.request(CODE.SEND_MESSAGE, data);
    }
    /**@returns {Promise<IViewMessageResponse>} */
    viewMessage(data) {
        return socket.request(CODE.VIEW_MESSAGE, data);
    }
    /**@returns {Promise<IViewPlusNoteResponse>} */
    viewPlusNote() {
        return socket.request(CODE.VIEW_PLUS_NOTE, {});
    }
    /**@returns {Promise<IViewPlusNoteResponse>} */
    savePlusNote(data) {
        return socket.request(CODE.SAVE_PLUS_NOTE, data);
    }

    join(uid = "", server = -1, authorized = false, activeVillageId = -1) {
        this.uid = uid;
        this.serverNumber = server;
        this.authorized = authorized === true;
        this.activeVillageId = activeVillageId;
        window.localStorage.setItem("uid", uid);
        window.localStorage.setItem("vid", activeVillageId);
        this.ready = true;
    }
    /** @param {Number} server * @param {Boolean} authorized * @param {String} key * @param {Number} villageId */
    updateAuthorization(server, authorized, key, villageId) {
        this.serverNumber = server;
        this.authorized = authorized === true;
        this.key = key;
        this.activeVillageId = villageId;
        window.localStorage.setItem("key", key);
        window.localStorage.setItem("vid", villageId);
    }
    setState(authorized = false, uid = "", key = "") {
        this.authorized = authorized === true;
        this.uid = uid;
        this.key = key;
        window.localStorage.setItem("uid", uid);
        window.localStorage.setItem("key", key);
    }

    GET(opcode, onLoad, data = {}) {
        return socket.cached(opcode, data, onLoad, onLoad);
    }
}

const client = new Client();

export default client;