function handleArray(array = []) {
    let result = [];
    array.forEach(prop => {
        if (Array.isArray(prop))
            result.push(handleArray(prop));
        else if (typeof prop === "object" && prop !== null)
            result.push(handleObject(prop));
        else
            result.push(typeof prop === "boolean" ? (prop ? 1 : 0) : prop);
    });
    return result;
}
function handleObject(obj = {}) {
    let result = [];
    Object.keys(obj).forEach(key => {
        let prop = obj[key];
        if (Array.isArray(prop))
            result.push(handleArray(prop));
        else if (typeof prop === "object" && prop !== null)
            result.push(handleObject(prop));
        else
            result.push(typeof prop === "boolean" ? (prop ? 1 : 0) : prop);
    });
    return result;
}
/** @param {{}|[]} data */
function minimize(data) {
    try {
        if (Array.isArray(data))
            return handleArray(data);
        else
            return handleObject(data);
    } catch (e) {
        console.error(e);
    }
}

module.exports = minimize;